
.vux-demo {
  text-align: center;
}
.backgroundImageFilter,
.container {
  width: 360px;
  height: 300px;
  font-size: 30px;
  text-align: center;
  line-height: 300px;
}
.container {
  margin: 0 auto;
  position: relative;
}
.backgroundImageFilter {
  position: absolute;
  background-image: url('https://i.postimg.cc/Z5FFjDDN/f37931a26309b94c4dd14fdd35e2aff.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  z-index: -1;
  -webkit-transition: -webkit-filter 1s;
  transition: -webkit-filter 1s;
  transition: filter 1s;
  transition: filter 1s, -webkit-filter 1s;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}
/*.container:hover  .backgroundImageFilter {*/
/*filter: blur(20px);*/
/*}*/
.previewer-demo-img{
  border: 5px solid #c5dce9;
  width: 80px;
  height: 80px;
  border-radius: 5px;
  margin-left: 5px;
}
/*masker*/
.m-img {
  padding-bottom: 33%;
  display: block;
  position: relative;
  max-width: 100%;
  background-size: cover;
  background-position: center center;
  cursor: pointer;
  border-radius: 2px;
}
.m-title {
  color: #fff;
  text-align: center;
  text-shadow: 0 0 2px rgba(0, 0, 0, .5);
  font-weight: 500;
  font-size: 16px;
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.m-time {
  font-size: 12px;
  padding-top: 4px;
  border-top: 1px solid #f0f0f0;
  display: inline-block;
  margin-top: 5px;
}
.card-padding {
  padding: 35px;
}



